import "./App.css";
import Login from "./components/Login";
import Signup from "./components/Signup";
import { Routes, Route } from "react-router-dom";
import Home from "./components/Home";
import SellerLogin from "./components/SellerLogin";
import SellerSignup from "./components/SellerSignup";
import SellerPage from "./components/SellerPage";
import CreateProduct from "./components/CreateProduct";
import UpdateProduct from "./components/UpdateProduct";
import NotFound from "./components/NotFound";
import ProductLists from "./components/ProductLists";
import ProductPage from "./components/ProductPage";
import LoadingBar from "react-top-loading-bar";
import { useState } from "react";

const App = () => {
  const [progress, setProgress] = useState(0);
  return (
    <div>
        <LoadingBar color="red" progress={progress} />
      <Routes>
        {/* User Pages */}
        <Route element={<Login />} exact path="/login" />
        <Route element={<Signup />} exact path="/signup" />
        <Route element={<Home />} exact path="/" />
        <Route element={<NotFound />} path="/*" />
        <Route
          element={
            <ProductLists
              setProgress={setProgress}
              category="mobiles"
              key="mobiles"
            />
          }
          exact
          path="/mobiles"
        />
        <Route
          element={
            <ProductLists setProgress={setProgress} category="pc" key="pc" />
          }
          exact
          path="/pc"
        />
        <Route
          element={
            <ProductLists
              setProgress={setProgress}
              category="electronics"
              key="electronics"
            />
          }
          exact
          path="/electronics"
        />
        <Route
          element={
            <ProductLists
              setProgress={setProgress}
              category="accessories"
              key="accessories"
            />
          }
          exact
          path="/accessories"
        />
        <Route
          element={<ProductPage />}
          key="productpage"
          exact
          path="/product/:id"
        />

        {/* Seller Pages */}
        <Route element={<SellerLogin />} exact path="/seller/login" />
        <Route element={<SellerSignup />} exact path="/seller/signup" />
        <Route element={<SellerPage />} exact path="/seller" />
        <Route element={<CreateProduct />} exact path="/seller/product" />
        <Route
          element={<UpdateProduct />}
          exact
          path="/seller/product/update/:productId"
        />
      </Routes>
    </div>
  );
};
export default App;
