import React, { useCallback, useEffect, useState } from "react";
import "../style/createproduct.css";
import { useNavigate, useParams } from "react-router-dom";

const UpdateProduct = () => {
  const host = process.env.REACT_APP_HOST;
  const navigate = useNavigate();
  const token = localStorage.getItem("gadgetstore-seller-token");
  let { productId } = useParams();
  const fetchData = useCallback(()=> {
     fetch(`${host}/api/seller/product/fetch/${productId}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      token: token,
    },
  })
    .then((res) => res.json())
    .then((jsonData) => {
      if (jsonData.success) {
        setProductData({
          oname: jsonData.product.name,
          obrand: jsonData.product.brand,
          ocategory: jsonData.product.category,
          odiscount: jsonData.product.discount,
          osubCategory: jsonData.product.subCategory,
          odescription: jsonData.product.description,
          oimages: jsonData.product.images,
          oprice: jsonData.product.price, 
          ostock: jsonData.product.stock,
          omore: jsonData.product.more
        });
      }
    });
  }, [token, productId, host]);
  useEffect(() => {
    if (!token) {
      navigate("/seller/login");
    }
    fetchData();
  }, [fetchData, navigate, token]);
  const [productData, setProductData] = useState({
    oname: "",
    obrand: "",
    ocategory: "",
    odescription: "",
    osubCategory: "",
    odiscount: "",
    oimages: [],
    oprice: "",
    ostock: "",
    omore: "",
  });
  const [updatedData, setUpdatedData] = useState({
    name: "",
    brand: "",
    category: "",
    description: "",
    discount: "",
    subCategory: "",
    images: [],
    price: "",
    stock: "",
    more: "",
  });
  const [images, setImages] = useState([]);
  const [delTokens, setDelTokens] = useState([]);
  const handleChange = (e) => {
    const { name, value } = e.target;
    setUpdatedData({
      ...updatedData,
      [name]: value,
    });
  };
  const handleImageUpload = (e) => {
    e.preventDefault();
    const files = Array.from(e.target.files);
    files.forEach((file) => {
      const data = new FormData();
      data.append("file", file);
      data.append("upload_preset", "gadget-store");
      fetch("https://api.cloudinary.com/v1_1/dv3qbj0bn/image/upload", {
        method: "POST",
        body: data,
      })
        .then((res) => res.json())
        .then((resData) => {
          const url = resData.url;
          const token = resData.delete_token;
          setImages((images) => [...images, url]);
          setDelTokens((delTokens) => [...delTokens, token]);
          const file = document.getElementById("image-input");
          file.value = "";
        })
        .catch((err) => console.log(err));
    });
  };
  const handleRemoveImage = async (fileToRemove) => {
    const index = images.indexOf(fileToRemove);
    const data = new FormData();
    data.append("token", delTokens[index]);
    fetch("https://api.cloudinary.com/v1_1/dv3qbj0bn/delete_by_token", {
      method: "POST",
      body: data,
    })
      .then((res) => res.json())
      .then((jsonData) => {
        if (jsonData.result === "ok") {
          const updatedImages = images.filter(
            (file) => file !== fileToRemove
          );
          setImages(updatedImages);
        }
      })
      .catch((err) => console.log(err));
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    // const data = {
    //   name: updatedData.name,
    //   category: updatedData.category,
    //   description: updatedData.description,
    //   images: images,
    //   price: updatedData.price,
    //   stock: updatedData.stock,
    //   more: updatedData.more,
    // };
    // fetch(`${host}/api/seller/product/create`, {
    //   method: "POST",
    //   headers: {
    //     "Content-Type": "application/json",
    //     token: token,
    //   },
    //   body: JSON.stringify(data),
    // })
    //   .then((res) => res.json())
    //   .then((jsonData) => {
    //     if (jsonData.success) {
    //       navigate("/seller");
    //     }
    //   });
  };
  const isFormValid =
    updatedData.name.length >= 3 &&
    updatedData.category.length >= 1 &&
    updatedData.description.length >= 10 &&
    updatedData.price > 0 &&
    updatedData.brand > 0 &&
    updatedData.stock > 0;
  return (
    <div className="create-product">
      <h2>Update Product</h2>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label className="label-createproduct">Name:</label>
          <input
            type="text"
            name="name"
            className="input-createproduct"
            value={productData.oname}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label className="label-createproduct">Brand:</label>
          <input
            type="text"
            name="brand"
            className="input-createproduct"
            value={productData.obrand}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label className="label-createproduct">Discount:</label>
          <input
            type="text"
            name="discount"
            className="input-createproduct"
            value={productData.odiscount}
            onChange={handleChange}
          />
        </div>
        <div className="form-group">
          <label className="label-createproduct">Category:</label>
          <input
            type="text"
            name="category"
            className="input-createproduct"
            value={productData.ocategory}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label className="label-createproduct">Sub Category:</label>
          <input
            type="text"
            name="subCategory"
            className="input-createproduct"
            value={productData.osubCategory}
            onChange={handleChange}
          />
        </div>
        <div className="form-group">
          <label className="label-createproduct">Description:</label>
          <textarea
            name="description"
            className="input-createproduct"
            value={productData.odescription}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label className="label-createproduct">Images:</label>
          <input
            type="file"
            name="images"
            className="input-createproduct"
            multiple
            id="image-input"
            onChange={handleImageUpload}
            accept="image/*"
          />
          <div className="images-glare">
            {productData.oimages &&
              productData.oimages.map((image, index) => {
                return (
                  <div key={index} className="img-with-btn">
                    <a href={image}>
                      <img
                        className="small-img"
                        alt={image + index}
                        src={image}
                        target="_blank"
                        rel="noreferrer"
                      />
                    </a>
                    <button
                      className="create-product-button remove-btn"
                      type="button"
                      onClick={() => handleRemoveImage(image)}
                    >
                      Remove
                    </button>
                  </div>
                );
              })}
          </div>
        </div>
        <div className="form-group">
          <label className="label-createproduct">Price:</label>
          <input
            type="number"
            name="price"
            className="input-createproduct"
            value={productData.oprice}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label className="label-createproduct">Stock Units:</label>
          <input
            type="number"
            name="stock"
            className="input-createproduct"
            value={productData.ostock}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label className="label-createproduct">More:</label>
          <input
            type="text"
            name="more"
            className="input-createproduct"
            value={productData.omore}
            onChange={handleChange}
          />
        </div>
        <button type="submit" disabled={!isFormValid} onClick={handleSubmit}>
          Create Product
        </button>
        <button type="button" className="remove-btn cancel-btn">
          Cancel
        </button>
      </form>
    </div>
  );
};

export default UpdateProduct;
