import React, { useEffect, useState, useRef } from "react";
import { useNavigate, Link } from "react-router-dom";
import gsap from "gsap";
import "../style/login.css";

function Login() {
  const host = process.env.REACT_APP_HOST;
  const navigate = useNavigate();
  const [disabled, setDisabled] = useState("disable");
  const [creds, setCreds] = useState({
    email: "",
    password: "",
  });
  const onChange = (e) => {
    e.preventDefault();
    setCreds({ ...creds, [e.target.name]: e.target.value });
    if(creds.email.length > 5 && creds.password.length >= 6){
      setDisabled("");
    }
    else{
      setDisabled("disable");
    }
  };
  const handleLogin = async (e) => {
    e.preventDefault();
    const response = await fetch(`${host}/api/auth/login`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        email: creds.email,
        password: creds.password,
      }),
    });
    const tokenResponse = await response.json();
    localStorage.setItem("gadgetstore-user-token", tokenResponse.token);
    navigate("/");
  };
  const app = useRef();
  useEffect(() => {
    let ctx = gsap.context(() => {
      gsap.from("#circle1", {
        x: -800,
        duration: 2,
      });
      gsap.from("#circle2", {
        x: 100,
        y: 100,
        duration: 2,
      });
      gsap.from("#circle3", {
        x: -100,
        y: -100,
        duration: 2,
      });
      gsap.from("#circle4", {
        x: 800,
        duration: 2,
      });
      gsap.from("#circle5", {
        y: -200,
        duration: 2,
      })
    }, app);

    return () => ctx.revert();
  }, []);

  return (
    <div className="main" ref={app}>
      <div id="circle1"></div>
      <div id="circle2"></div>
      <div id="circle3"></div>
      <div id="circle4"></div>
      <div id="circle5"></div>
      <div id="box">
        <h1 id="logintext">Login</h1>
        <form onSubmit={handleLogin}>
          <input
            className="bar"
            name="email"
            type="email"
            placeholder="Email"
            onChange={onChange}
          />
          <input
            className="bar"
            name="password"
            type="password"
            placeholder="Password"
            onChange={onChange}
          />
          <button
            type="submit"
            className={`bar ${disabled}`}
            id="loginbtn"
          >
            Login
          </button>
        </form>
        <span id="register">
          Don't have an account, <Link to="/signup">Register</Link>
        </span>
      </div>
    </div>
  );
}

export default Login;
