import React, { useCallback, useEffect, /*useRef*/ useState } from "react";
import Product from "./Product";
import "../style/accessories.css";

const Accessories = (props) => {
  const host = process.env.REACT_APP_HOST;
  // const Ref1 = useRef();
  // const Ref2 = useRef();
  const slideLeft = (i) => {

  };
  const slideRight = (i) => {

  };
  const [results_1, setResults_1] = useState([]);
  // const [results_2, setResults_2] = useState([]);
  // API Call for both categories
  const fetchData = useCallback(() => {
    fetch(`${host}/api/client/home?category=${props.category}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((jsonData) => jsonData.json())
      .then((data) => {
        if (data.success) {
          setResults_1(data.products);
        }
      })
      .catch((err) => console.log(err));
    }, [host, props.category]);
  useEffect(() => {
    fetchData();
  }, [fetchData]);
  return (
        <div className="box">
          <h3>{props.heading}</h3>
          <div className="flexbox smallbox">
            <button
              className={`btn-flexbox left-flexbox a0-left ${
                results_1.length > 1 ? "" : "d-none"
              }`}
              onClick={() => slideLeft(0)}
            >
              <i className="fa fa-chevron-left"></i>
            </button>
            <button
              className={`btn-flexbox right-flexbox a0-right ${
                results_1.length > 1 ? "" : "d-none"
              }`}
              onClick={() => slideRight(0)}
            >
              <i className="fa fa-chevron-right"></i>
            </button>
            {results_1 &&
              results_1.map((result_1) => {
                return (
                  <Product
                    img={result_1.images[0]}
                    alt={result_1.name}
                    name={result_1.name}
                    detail={result_1.more}
                    link={result_1._id}
                    key={result_1._id}
                  />
                );
              })}
          </div>
        </div>
        
  );
};

export default Accessories;
